h1{color:#434c5e;font-size:3.5em;margin-left:0.5em}.link{text-decoration:none;font-weight:bold;color:#434c5e}.link:hover{color:#a3be8c}.intro{text-align:left;font-size:1.3em;margin:0.5em;margin-bottom:1.5em}.avatar{background-repeat:no-repeat;background-position:center;background-image:url(https://avatars1.githubusercontent.com/u/47358668?s=460&v=4);background-size:cover;width:120px;height:120px;flex-shrink:0;border-radius:50%;margin:1em}.banner{text-align:center;margin-top:2em;margin-bottom:1em;justify-content:flex-start;display:flex}

.terminal-head{height:28px;background-color:#2e3440;border-radius:10px 10px 0 0;display:flex;justify-content:flex-end;align-items:center}h4{color:#88c0d0}.close{height:15px;width:15px;background-color:#bf616a;border-radius:50%;display:inline-block;margin:10px}.max{height:15px;width:15px;background-color:#a3be8c;border-radius:50%;display:inline-block}.min{height:15px;width:15px;background-color:#ebcb8b;border-radius:50%;display:inline-block;margin:10px}.card{background:#434c5e;box-shadow:0 8px 16px 0 rgba(0,0,0,0.2);flex:0 1 24%;border-radius:0 0 10px 10px;min-height:500px;margin-top:0rem;margin-bottom:0rem}

.text{padding:1em;padding-top:0.5em;font-size:1rem;color:#eceff4;font-family:Monospace}.projects{color:#81a1c1}p{margin-top:0em;line-height:2.5rem;color:#eceff4}a{text-decoration:none;color:#a3be8c}.cursor{background:rgba(247,247,247,0.65);-webkit-animation:blinker 1s linear infinite;animation:blinker 1s linear infinite}@-webkit-keyframes blinker{50%{opacity:0}}@keyframes blinker{50%{opacity:0}}

.title{text-align:center}.projectcard{min-width:200px;min-height:50px;border-radius:50px;background:linear-gradient(145deg, #b7a7a7, #9a8c8c);box-shadow:30px 30px 60px #918585, -30px -30px 60px #c5b3b3;margin-bottom:2rem}h2{color:#434c5e;font-size:2em}.button{background-color:#434c5e;border:none;margin-top:2rem;margin-right:1rem;color:white;padding:0.5rem 0.5rem;text-align:center;text-decoration:none;display:inline-block;font-size:1rem;transition-duration:0.4s;cursor:pointer}.button1{border:0.2rem solid #a3be8c}.button1:hover{background-color:#a3be8c;color:white}.button2{border:0.2rem solid #d08770}.button2:hover{background-color:#d08770;color:white}

.container{width:90%;max-width:860px;margin:0 auto;display:flex;flex-direction:column}body{background:#d8dee9;margin:0;font-family:-apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif}

